
@charset "UTF-8";
.el-notification{width:330px;padding:20px;box-sizing:border-box;border-radius:2px;position:fixed;right:16px;background-color:#fff;box-shadow:0 2px 4px rgba(0,0,0,.12),0 0 6px rgba(0,0,0,.04);transition:opacity .3s,right .3s,top .4s,-webkit-transform .3s;transition:opacity .3s,transform .3s,right .3s,top .4s;transition:opacity .3s,transform .3s,right .3s,top .4s,-webkit-transform .3s;overflow:hidden
}
.el-notification .el-icon-circle-check{color:#13ce66
}
.el-notification .el-icon-circle-cross{color:#ff4949
}
.el-notification .el-icon-information{color:#50bfff
}
.el-notification .el-icon-warning{color:#f7ba2a
}
.el-notification__group{margin-left:0
}
.el-notification__group.is-with-icon{margin-left:55px
}
.el-notification__title{font-weight:400;font-size:16px;color:#1f2d3d;margin:0
}
.el-notification__content{font-size:14px;line-height:21px;margin:10px 0 0;color:#8391a5;text-align:justify
}
.el-notification__icon{width:40px;height:40px;font-size:40px;float:left;position:relative;top:3px
}
.el-notification__closeBtn{top:20px;right:20px;position:absolute;cursor:pointer;color:#bfcbd9;font-size:14px
}
.el-notification__closeBtn:hover{color:#97a8be
}
.el-notification-fade-enter{-webkit-transform:translateX(100%);transform:translateX(100%);right:0
}
.el-notification-fade-leave-active{opacity:0
}


